<template>
  <div class="qiquanaccount">
    <el-form :inline="true" :model="formInline" class="demo-form-inline">
      <el-form-item label="姓名">
        <el-input v-model="formInline.employeeName" placeholder="请输入员工姓名"></el-input>
      </el-form-item>
      <!-- 选择年份 -->
      <!-- <el-form-item >
        <span>年</span>
        <el-date-picker  v-model="countYear" type="year" placeholder="选择年"></el-date-picker>
      </el-form-item> -->

      <el-form-item>
        <el-button type="primary" @click="search">搜索</el-button>
        <el-button type="primary" @click="grant">发放</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="tableData"
      header-align="center"
      :cell-style="cellHeight"
      fit
      @selection-change="accountSelectionChange"
    >
      <el-table-column prop="date" align="center" label="基本信息">
        <el-table-column type="selection"></el-table-column>
        <el-table-column align="center" label="序号">
          <template slot-scope="scope">{{ scope.row.number }}</template>
        </el-table-column>
        <el-table-column prop="employee.name" align="center" label="姓名"></el-table-column>
        <el-table-column prop="employee.sex" align="center" label="性别"></el-table-column>
        <el-table-column prop="employee.idCard" align="center" label="身份证号码" width="170"></el-table-column>
        <el-table-column prop="importCompany.name" align="center" label="公司"></el-table-column>
        <el-table-column prop="employee.postJob" align="center" label="岗位"></el-table-column>
        <el-table-column prop="employee.postArea" align="center" width="100" label="人员范围"></el-table-column>
        <!-- <el-table-column prop="employee.jobNumber" align="center" label="工号"></el-table-column> -->
        <el-table-column prop="employee.seniority" align="center" label="工龄"></el-table-column>
        <el-table-column prop="sendSts" align="center" label="发放状态"></el-table-column>
      </el-table-column>
      <el-table-column prop="province" align="center" label="可分配金额">
        <!-- <el-table-column prop="equityActualMoney" align="center" width="100" label="应发金额"></el-table-column> -->
         
        <el-table-column prop="equityShouldMoney" width="100" align="center" label="应发金额" ></el-table-column>      
        <el-table-column prop="equityActualMoney" align="center" width="130" label="实际发放金额" ></el-table-column>
      </el-table-column>

      <el-table-column prop="province" align="center" label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="countDetail(scope)" size="small">详情</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[6, 10, 15, 20]"
      :page-size="10"
      :total="this.total"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>
    <!-- 详情 -->
    <div class="countDetail" v-if="isDetail">
      <div
        style="width:100%;text-align:center;height:40px;line-height:40px;border-radius:5px;background:#409EFF;margin-bottom:10px;"
      >人员详情</div>
      <el-table :data="countDetailData" border>
        <el-table-column prop="sendYear" label="兑换年度"></el-table-column>
        <el-table-column prop="equityShouldMoney" label="应发金额"></el-table-column>
        <el-table-column prop="equityActualMoney" label="实际发放金额"></el-table-column>
      </el-table>
      <el-button type="primary" style="margin:15px 0 15px 0;" @click="countexit">退出</el-button>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  name: "city",
  data() {
    return {
      input: "",

      // countYear: new Date().getFullYear().toString(),// 核算年份
      employeeName: "",
      actualSendNums: [],
      employeeId: "", // 员工id
      countSendID: "", // 企权发放Id
      tableData: [], // 表格数据
      countDetailData: [], // 详情表格数据
      ids: [], // 选中的ids数组
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 从哪一个开始
      pageNum: 10, // 一次请求多少
      // loading: true,
      isDetail: false,
      formInline: {
        employeeName: ""
      }
    };
  },
  created() {
     this.getaccount();
  },
  methods: {
    cellHeight() {
      return "padding: 0 !important";
    },
    // 获取选中的ids
    accountSelectionChange(val) {
      console.log(val);
      this.ids = [];
      let len = val.length;
      for (let i = 0; i < len; i++) {
        this.ids.push(val[i].id);
      }
      console.log("ids", this.ids);
    },
    // 发放
    grant() {
      this.$axios
        .put(
          "/employeeEquityCount/send",
          qs.stringify(
            {
              countIds: this.ids,
              type: 1
            },
            { indices: false }
          )
        )
        .then(res => {
          this.$message({
            message: res.data.msg
          });
          this.getaccount();
          console.log("企权发放", res);
        })
        .catch(erro => {
          console.log("请求失败", erro);
        });
    },
    // 根据名字搜索
    search() {
      this.pageSize = 1;
      this.pageNum = 10;
      this.$axios
        .get("/employeeEquityCount/counting/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize,
            // countYear: this.countYear,
            countYear: 2019,
            employeeName: this.formInline.employeeName
          }
        })
        .then(res => {
          console.log("请求成功", res);
          this.total = res.data.total;
          this.tableData = res.data.data;
          this.$set(this.tableData, "number");
          for (var i = 0, len = this.tableData.length; i < len; i++) {
            const employee = this.tableData[i].employee;
            this.tableData[i].number = i + 1;
            if (employee.sex == "0") {
              employee.sex = "男";
            } else {
              employee.sex = "女";
            }
            if (this.tableData[i].sendSts == "Y") {
              this.tableData[i].sendSts = "已发放";
            } else if (this.tableData[i].sendSts == "N") {
              this.tableData[i].sendSts = "未发放";
            }
          }
        })
        .catch(erro => {
          console.log("请求失败", erro);
        });
    },

    // 获取实际输入金额
    handleinput(scope) {
      console.log(scope, "scope");
      this.ids = scope.row.id;
      this.actualSendNums = scope.row.actualSend;
      console.log(this.actualSendNums, this.ids, "actualSendNums");

      // 确认修改实际核算,并请求
      this.$confirm("此操作将修改实际发放金额, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.actualSend();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消修改"
          });
        });
    },

    // 获取核算列表
    getaccount() {
      this.pageNum = 10;
      this.pageSize = 1;
      this.$axios
        .get("/employeeEquityCount/counting/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize,
            countYear: 2019,
            // countYear: this.countYear
          }
        })
        .then(res => {
          console.log("请求成功", res);
          this.total = res.data.total;
          this.tableData = res.data.data;
          this.$set(this.tableData, "number");
          for (var i = 0, len = this.tableData.length; i < len; i++) {
            const employee = this.tableData[i].employee;
            this.tableData[i].number = i + 1;
            if (employee.sex == "0") {
              employee.sex = "男";
            } else {
              employee.sex = "女";
            }
            if (this.tableData[i].sendSts == "Y") {
              this.tableData[i].sendSts = "已发放";
            } else if (this.tableData[i].sendSts == "N") {
              this.tableData[i].sendSts = "未发放";
            }
          }
        })
        .catch(erro => {
          console.log("请求失败", erro);
        });
    },
    // 实际发放金额数目
    actualSend() {
      this.$axios
        .put(
          "/employeeEquityCount/actual/send/" +
            this.ids +
            "?" +
            "actualMoney=" +
            this.actualSendNums
        )
        .then(res => {
          console.log("请求成功", res);
          // 刷新请求列表
          this.getaccount();
          this.$message({
            type: "success",
            message: res.data.msg
          });
        })
        .catch(erro => {
          console.log("请求失败", erro);
        });
    },
    // 查看历年发放明细
    countDetail(val) {
      console.log(val, "val");
      this.isDetail = true;
      this.employeeId = val.row.employeeId;
      this.$axios
        .get("/employeeEquityCount/detail/" + this.employeeId)
        .then(res => {
          this.countDetailData = res.data.data;
          console.log("查看历年发放明细", res);
        })
        .catch(erro => {
          console.log("请求失败", erro);
        });
    },
    // 退出详情页
    countexit() {
      this.isDetail = false;
    },

    // 企权发放按钮
    countSend(val) {
      console.log(val);
      this.countSendID = val.row.id;
      this.$axios
        .put("/employeeEquityCount/send/" + this.countSendID)
        .then(res => {
          console.log("企权发放", res);
        })
        .catch(erro => {
          console.log("请求失败", erro);
        });
    },

    // 分页器
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.getaccount();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.getaccount();
    }
  }
};
</script>

<style lang="scss" scoped>
.qiquanaccount {
  .countDetail {
    position: absolute;
    height: 500px;
    overflow: auto;
    top: 10px;
    left: 10px;
    right: 10px;
    bottom: 0;
    z-index: 100;
    padding: 30px;
    background: white;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
}
</style>


