 <template>
  <div class="personMarage">
    <el-button-group style="padding-bottom: 5px">
      <el-button type="primary" @click="putAudit()">
        提交
      </el-button>
      <el-button type="primary" @click="removeBatch()">
        删除
      </el-button>
    </el-button-group>

    <el-table
      :data="list"
      border
      stripe
      highlight-current-row
      :cell-style="cellHeight"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column  label="序号" width="80">
        <template slot-scope="scope">{{ scope.row.number }}</template>
      </el-table-column>
      <el-table-column align="center" prop="name" width="100" label="姓名"></el-table-column>
      <el-table-column align="center" prop="sex" label="性别"></el-table-column>
      <el-table-column align="center" prop="idCard" width="170px" label="身份证号码"></el-table-column>
      <el-table-column align="center" prop="joinDate" width="130px" label="入职时间"></el-table-column>
      <el-table-column align="center" prop="postJob" width="60px" label="岗位"></el-table-column>
      <el-table-column align="center" prop="postArea" width="130px" label="人员范围"></el-table-column>
      <el-table-column align="center" prop="seniority" width="50px" label="工龄"></el-table-column>
      <el-table-column align="center" prop="auditSts" width="100px" label="状态"></el-table-column>
      <el-table-column align="center" prop="createDate" width="170px" label="创建时间"></el-table-column>
      <el-table-column align="center" fixed="right" width="140px" label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[6, 10, 15, 20]"
      :page-size="10"
      :total="this.total"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>

    <div v-if="this.$store.state.newEmployeeIsShow" class="newEmployee">
      <NewEmployee @refresh="getPersonList"></NewEmployee>
    </div>
  </div>
</template>

<script>
import NewEmployee from "./editEmployee/index";
import qs from "qs";

// 局部注册过滤器
// import { formatDate } from "@/utils/date.js";

export default {
  components: {
    NewEmployee
  },
  name: "city",

  provide(){
    return{
      reload:this.reload
    }
  },

  data() {
    return {
      list: [],
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 从哪一个开始
      pageNum: 10, // 一次请求多少
    };
  },
  // 将时间戳转换时间格式局部注册
  // filters: {
  //   formatDate(time) {
  //     var date = new Date(time);
  //     return formatDate(date, "yyyy-MM-dd"); // 年月日 格式自己定义   'yyyy : MM : dd'  例 2018年12月5日的格式
  //   },
  //   formatDateTwo(time) {
  //     var date = new Date(time);
  //     return formatDate(date, "hh:mm:ss"); // 时间点 例 21点12分12秒的格式
  //   }
  // },

  created() {
    console.log("父组件")
    this.getPersonList();

    // 局部过滤器使用方法
    // console.log(this.$root.$options.filters.formatDate(1646461131351));
  },


  methods: {
    cellHeight() {
      return "padding: 0 !important";
    },
    // 重新刷新, provide/inject
      reload(){
      this.$store.state.newEmployeeIsShow = false;
      this.$nextTick(function(){
        this.$store.state.newEmployeeIsShow = true;
      })
    },

    // 获取员工列表
    getPersonList() {
      this.$axios
        .get("/employee/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize
          }
        })
        .then(res => {
          console.log("人员列表", res);
          this.total = res.data.total;
          let employeeData = res.data.data;
          this.$set(employeeData, "number");
          for (var j = 0, len = employeeData.length; j < len; j++) {
            let filters = this.$root.$options.filters;
            employeeData[j].createDate = filters.formatDate(
              employeeData[j].createDate
            );
            employeeData[j].number = j + 1;
            if (employeeData[j].sex == "0") {
              employeeData[j].sex = "男";
            } else {
              employeeData[j].sex = "女";
            }
             switch (employeeData[j].auditSts) {
                 case "C":
                employeeData[j].auditSts = "待审核";
                break;
              case "X":
                employeeData[j].auditSts = "新建";
                break;
              case "N":
                employeeData[j].auditSts = "审核不通过";
                break;
              case "Y":
                employeeData[j].auditSts = "审核通过";
                break;
            };
            this.list = employeeData;
          }
        })
        .catch(erro => {
          console.log(erro);
        });
    },

    // 分页器
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.getPersonList();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.getPersonList();
    },

    // 修改
    handleEdit(index, row) {
      console.log(index, row);
      console.log("修改");
      this.$store.state.newEmployee = row;
      this.$store.state.newEmployeeIsShow = true;
    },

    //   选中获取多选ids
    handleSelectionChange(val) {
      this.ids = [];
      let len = val.length;
      for (let i = 0; i < len; i++) {
        this.ids.push(val[i].id);
      }
      console.log("ids", this.ids);
    },
    // 批量审核
    putAudit() {
      this.$axios
        .put(
          "/employee/putAudit",
          qs.stringify(
            {
              employeeIds: this.ids,
              type: 1
            },
            { indices: false }
          )
        )
        .then(res => {
          console.log("提交审核", res);
          this.$message.info("审核成功");
          this.getPersonList();
        })
        .catch(erro => {
          console.log(erro);
        });
    },
    // 批量删除
    removeBatch() {
      this.$axios
        .delete("/employee/removeBatch", {
          params: {
            employeeIds: this.ids,
            type: 1
          },
          paramsSerializer: params => {
            return qs.stringify(params, { indices: false });
          }
        })
        .then(res => {
          console.log("批量删除", res);
          this.$message.info(res.data.msg);
          this.getPersonList();
        })
        .catch(erro => {
          console.log(erro);
        });
    },

    // 测试删除
    // refresh2(){
    //   console.log("刷新.......")
    // }
  }
};
</script>

<style lang="scss" scoped>
.personMarage {
  .newEmployee {
    position: absolute;
    height: 500px;
    overflow: auto;
    top: 1px;
    left: 50px;
    right: 50px;
    bottom: 60px;
    z-index: 100;
    padding: 15px;
    background: white;
    border-radius: 9px;
    border: 1px solid #ebeef5;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
}
</style>


