<template>
  <div class="gangumessage">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">


          <el-form-item label="行业" prop="industry">
            <el-input readonly v-model="form.industry"></el-input>
          </el-form-item>
 
          <el-form-item label="兑现年度" prop="cashDate">
            <el-date-picker
              v-model="form.cashDate"
              type="year"
              value-format="yyyy"
              placeholder="选择兑现年度"
            ></el-date-picker>
          </el-form-item>

    
          <el-form-item label-width="150px" label="批复文号" prop="approvalCode">
            <el-input v-model="form.approvalCode"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="批复可分配的总金额" prop="approvalMoney">
            <el-input v-model="form.approvalMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="审计期间" prop="auditPeriod">
            <el-date-picker
              v-model="form.auditPeriod"
              type="monthrange"
              align="right"
              value-format="yyyyMM"
              unlink-panels
              range-separator="至"
              start-placeholder="开始月份"
              end-placeholder="结束月份"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label-width="150px" label="可享受的干股金额" prop="sharePerformanceMoney">
            <el-input v-model="form.sharePerformanceMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="可享受的月份数" prop="shareMonthNumber">
            <el-input v-model="form.shareMonthNumber"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="可享受的干股比例" prop="shareRatio">
            <el-input v-model="form.shareRatio" placeholder="例如:0.14"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="审计期间月份" prop="auditMonthNumber">
            <el-input v-model="form.auditMonthNumber"></el-input>
          </el-form-item>

      <el-button type="primary" @click="amend">修改</el-button>
      <el-button type="primary" @click="exit">退出</el-button>
    
    </el-form>
  </div>
</template>

<script>
export default {
   inject:['reload'],
  data() {
    return {
      employee: this.$store.state.gemployee,
      form: {
        approvalCode: this.$store.state.gemployee.approvalCode, // 批复文号
        industry: this.$store.state.gemployee.industry, // 行业
        shareMonthNumber: this.$store.state.gemployee.shareMonthNumber, // 可享受的月份数
        sharePerformanceMoney: this.$store.state.gemployee.sharePerformanceMoney, //可享受的干股金额
        shareRatio: this.$store.state.gemployee.shareRatio.replace("%","")/100, // 享受干股比率 如0.14
        approvalMoney: this.$store.state.gemployee.approvalMoney, // 批复金额
        auditMonthNumber: this.$store.state.gemployee.auditMonthNumber, // 审计区间月份数
        cashDate: this.$store.state.gemployee.cashDate, // 兑现年度
        auditPeriod: this.$store.state.gemployee.auditPeriod.split("-"), // 审计期间
      },
      rules: {
        industry: [
          { required: true, message: "请选择行业", trigger: "change" }
        ],
        auditPeriod: [
          { required: true, message: "请选择企权期间", trigger: "change" }
        ],
      
        cashDate: [
          { required: true, message: "请输入兑现年度", trigger: "blur" }
        ], 
        approvalCode: [
          { required: true, message: "请输入批复文号", trigger: "blur" }
        ],
        approvalMoney: [
          { required: true, message: "请输入批复可分配的金额", trigger: "blur" }
        ],
        sharePerformanceMoney: [
          { required: true, message: "可受企权的总金额", trigger: "blur" }
        ],
        shareMonthNumber: [
          { required: true, message: "可享受的月份", trigger: "blur" }
        ],
        shareRatio: [
          { required: true, message: "可享受的干股比例", trigger: "blur" }
        ],
        auditMonthNumber: [
          { required: true, message: "审计期间月份", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    // console.log(this.$store.state.gemployee.employeeIdCard);
  },

  methods: {
    amend() {
      console.log(this.$refs.form);
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log("修改之后的表格", this.form);
          this.form.auditPeriod = this.form.auditPeriod.join("-");
          this.form.performanceId = this.employee.performanceId;
          console.log(JSON.stringify(this.form));
          console.log(this.employee.id);
          this.$axios
            .put(
              "/performanceShares/edit/",
              JSON.stringify(this.form),
              {
                headers: {
                  "Content-Type": "application/json;charset=UTF-8"
                }
              }
            )
            .then(res => {
              console.log(res, "amend");
              // this.reload();
              this.$emit("refresh")
              this.form.shareRatio = this.form.shareRatio * 100 + "%";
               this.$store.state.gemployee = this.form;
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    change() {
      // 日期选择改变你呢函数
      console.log("change");
    },
    exit() {
      this.$store.state.gisshow = false;
    

    }
  }
};
</script>

<style lang="scss" scoped>
</style>


