 <template>
  <div class="annualQiquan">
    <el-form :model="Form" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <div style="border:1px solid gray; margin-top:10px;padding:30px ;">
        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label-width="170px" label="企权年度基本固定数据" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
            <el-form-item  label-width="170px" label="企权期间" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="13">
            <el-form-item label-width="150px" label="批复文号" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
            <el-form-item label-width="150px" label="批复可分配的总金额" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <div style="border:1px solid gray;margin: 30px 0; padding:30px ;">
        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label-width="170px" label="干股年度基本固定数据" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
            <el-form-item label-width="170px" label="审计期间" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="13">
            <el-form-item label-width="150px" label="批复文号" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
            <el-form-item label-width="150px" label="批复金额" prop="name">
              <el-input v-model="Form.name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </div>    
        <el-button  type="primary" @click="submitForm('Form')">提交</el-button>   
    </el-form>
  </div>
</template>

<script>
export default {
  name: "city",
  data() {
    return {
      msg: "我是年度基本信息输入页面..",
      Form: {
        name: ""
      },
      rules: {
        name: [{ required: true, message: "请选择行业", trigger: "change" }]
      }
    };
  },
  created() {},
  methods: {}
};
</script>

<style lang="scss" scoped>

</style>


