<template>
  <div class="qiquanmessage">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-form-item label-width="150px" label="行业" prop="industry">
        <el-input readonly v-model="form.industry"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="批复文号" prop="approvalCode">
        <el-input v-model="form.approvalCode"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="批复可分配的总金额" prop="approvalMoney">
        <el-input v-model="form.approvalMoney"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="企权期间" prop="equityPeriod">
        <el-date-picker
          v-model="form.equityPeriod"
          type="monthrange"
          align="right"
          value-format="yyyyMM"
          unlink-panels
          range-separator="至"
          start-placeholder="开始月份"
          end-placeholder="结束月份"
        ></el-date-picker>
      </el-form-item>

      <el-form-item label-width="150px" label="可受企权的总金额" prop="shareMoney">
        <el-input v-model="form.shareMoney"></el-input>
      </el-form-item>

      <el-form-item label-width="150px" label="兑现年度" prop="cashDate">
        <el-date-picker
          v-model="form.cashDate"
          type="year"
          value-format="yyyy"
          @change="change"
          placeholder="选择年"
        ></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="amend">修改</el-button>
        <el-button type="primary" @click="exit">退出</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import axios from "axios";
export default {
  inject: ["reload"],
  data() {
    return {
      employee: this.$store.state.employee,
      form: {
        industry: this.$store.state.employee.industry, //行业
        approvalCode: this.$store.state.employee.approvalCode, // 批复文号
        approvalMoney: this.$store.state.employee.approvalMoney, // 批复可分配的金额
        cashDate: this.$store.state.employee.cashDate, // 兑现年度
        shareMoney: this.$store.state.employee.shareMoney, // 可受企权的总金额
        equityPeriod: this.$store.state.employee.equityPeriod.split("-") // 期权期间
      },

      rules: {
         industry: [
          { required: true, message: "请选择行业", trigger: "change" }
        ],
        equityPeriod: [
          { required: true, message: "请选择企权期间", trigger: "change" }
        ],
        cashDate: [
          { required: true, message: "请输入兑现年度", trigger: "blur" }
        ],

        approvalCode: [
          { required: true, message: "请输入批复文号", trigger: "blur" }
        ],
        approvalMoney: [
          { required: true, message: "请输入批复可分配的金额", trigger: "blur" }
        ],
        shareMoney: [
          { required: true, message: "可受企权的总金额", trigger: "blur" }
        ]
      }
    };
  },
  created() {
    console.log(this.$store.state.employee);
    console.log(this.form);
  },
  methods: {
    amend() {
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log(this.form.equityPeriod);
          this.form.equityPeriod = this.form.equityPeriod.join("-");
          this.form.basicEquityId = this.employee.basicEquityId;
          console.log(this.form, "this.form");
          this.$axios
            .put("/basicEquity/edit/", JSON.stringify(this.form), {
              headers: {
                "Content-Type": "application/json;charset=UTF-8"
              }
            })
            .then(res => {
              console.log(res, "amend");
              this.$store.state.employee = this.form;
               this.$emit("refresh")
              // this.reload();
              console.log(this.$store.state.employee,this.form,"修改后赋值给state");
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          this.$message.info("提交失败,请重新提交");
          return false;
        }
      });
    },
    change() {
      // 日期选择改变你呢函数
      console.log("change");
    },
    exit() {
      this.$store.state.isshow = false;
     
      // console.log(this.$store.state.isshow)
      // this.reload();
      // this.getlist();
    }
  }
};
</script>

<style lang="scss" scoped>
</style>


