<template>
  <div class="home">
    <el-container>
      <!-- 头部 -->
      <el-header>
        <el-row>
          <el-col :span="24">
            <div class="headTop">
              <el-row>
                <el-col :span="2">
                  <div style="width:70px">
                    <img class="renbenIcon" src="../../images/renben.png" alt>
                  </div>
                </el-col>
                <el-col :span="10">
                  <div style="color:white;width:230px">人本集团财务部企权(干股系统)</div>
                </el-col>
                <el-col :span="12">
                  <div style="color:white;width:400px;position: absolute;right: 10px;">
                    <span v-if="islogin" style="font-size:14px;">账户: {{this.$store.state.username}}</span>
                    <span v-else style="font-size:14px;">账户:</span>
                    <span style="margin: 0 10px; font-size:14px ">所属公司: 浙江XXX有限公司</span>
                    <span
                      class="usermes"
                      style="padding:3px; font-size:14px;cursor:pointer;color:rgba(255,255,255,.8)"
                    >
                      修改密码
                      <i class="iconfont icon-shuxiebianji"></i>
                    </span>
                    <span
                      @click="exit"
                      class="usermes"
                      style="padding:3px; font-size:14px;cursor:pointer;color:rgba(255,255,255,.8);"
                    >
                      退出登录
                      <i class="iconfont icon-icon_logout"></i>
                    </span>
                  </div>
                </el-col>
              </el-row>
            </div>
          </el-col>
        </el-row>
      </el-header>
      <!-- 下面slider和main -->
      <el-container>
        <el-aside :class="{'isCollapse':isCollapse}">
          <el-menu
            class="el-menu-vertical-demo"
            :default-openeds="openeds"
            @open="handleOpen"
            @close="handleClose"
            background-color="#545c64"
            text-color="#fff"
            :collapse="isCollapse"
            active-text-color="#ffd04b"
            :collapse-transition="false"
          >
            <el-submenu index="1">
              <template slot="title">
                <i class="el-icon-menu common"></i>
                <span>基本模块</span>
              </template>
              <el-submenu index="2">
                <template slot="title">
                  <i class="el-icon-location common"></i>
                  <span>基本信息管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item @click="gotopath('personMarage')" index="2-1">
                    <i class="iconfont common icon-renliziyuan"></i>
                    人员管理
                  </el-menu-item>
                  <el-menu-item @click="gotopath('addEmployee')" index="2-2">
                    <i class="iconfont common icon-renliziyuan"></i>
                    添加员工
                  </el-menu-item>
                  <el-menu-item @click="gotopath('renli')" index="2-3">
                    <i class="iconfont common icon-renliziyuan"></i>
                    企权干股资格审核
                  </el-menu-item>
                  <el-menu-item @click="gotopath('qiquan')" index="2-4">
                    <i class="iconfont common icon-shuru"></i>
                    企权资本信息输入
                  </el-menu-item>
                  <el-menu-item @click="gotopath('gangu')" index="2-5">
                    <i class="iconfont common icon-shuru"></i>
                    干股基本信息输入
                  </el-menu-item>
                  <el-menu-item @click="gotopath('qiquansummary')" index="2-6">
                    <i class="iconfont common icon-biaoge"></i>
                    企权汇总表
                  </el-menu-item>
                  <el-menu-item @click="gotopath('gangusummary')" index="2-7">
                    <i class="iconfont common icon-biaoge"></i>
                    干股汇总表
                  </el-menu-item>
                  <el-menu-item @click="gotopath('annualbasinfo')" index="2-8">
                    <i class="iconfont common icon-shuxiebianji"></i>
                    年度基本数据录入
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>

              <el-menu-item @click="gotopath('staffMovement')" index="4">
                <i class="iconfont common icon-biaoge"></i>
                <span slot="title">人员调动汇总表</span>
              </el-menu-item>
              <el-menu-item @click="gotopath('dimission')" index="5">
                <i class="iconfont common icon-biaoge"></i>
                <span slot="title">离职汇总表</span>
              </el-menu-item>
              <el-menu-item @click="gotopath('jurisdiction')" index="6">
                <i class="iconfont common icon-jiaosequanxian"></i>
                <span slot="title">角色权限管理</span>
              </el-menu-item>
              <el-menu-item @click="gotopath('remind')" index="7">
                <i class="iconfont common icon-jinggaotianchong"></i>
                <span slot="title">提醒与警示</span>
              </el-menu-item>
            </el-submenu>

            <el-submenu index="8">
              <template slot="title">
                <i class="iconfont common icon-hesuanguanli"></i>
                <span>核算管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item @click="gotopath('qiquanAccountEntering')" index="8-2">
                  <i class="iconfont common icon-shuxiebianji"></i>
                  企权实际发放金额录入
                </el-menu-item>
                <el-menu-item @click="gotopath('qiquanaccount')" index="8-1">
                  <i class="iconfont common icon-hesuanguanli"></i>
                  企权核算
                </el-menu-item>
                <el-menu-item @click="gotopath('ganguAccountEntering')" index="8-4">
                  <i class="iconfont common icon-shuxiebianji"></i>
                  干股实际发放金额录入
                </el-menu-item>
                <el-menu-item @click="gotopath('ganguaccount')" index="8-3">
                  <i class="iconfont common icon-hesuanguanli"></i>
                  干股核算
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>

            <el-menu-item index="9">
              <i class="iconfont common icon-baobiaochaxun"></i>
              <span slot="title">报表查询</span>
            </el-menu-item>
          </el-menu>
          <div v-if="!isCollapse" class="copyright">
            <p>服务支持: 温州中本信息科技有限公司</p>
            <p>联系电话: 400-8095-669</p>
            <p>
              <span class="iconfont icon-banquan"></span>
              2018 人本集团有限公司 版权所有
            </p>
            <p>浙ICP备05070781号</p>
          </div>
        </el-aside>
        <el-main :class="{'maincollapse':isCollapse}">
          <!-- 面包屑 -->
          <div class="breadcrumb">
            <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
              <el-button style="border:none" size="small" @click="isCollapse=!isCollapse">
                <span style="font-size:20px" class="iconfont icon-moren_zhedie"></span>
              </el-button>
            </el-radio-group>
            <el-breadcrumb
              style="padding:9px 0;"
              class="breadcrumb-container"
              separator-class="el-icon-arrow-right"
            >
              <el-breadcrumb-item
                v-for="item in levelList"
                :key="item.path"
                :to="item.path"
              >{{""||item.meta.title}}</el-breadcrumb-item>
            </el-breadcrumb>
          </div>

          <div class="mainView">
            <transition name="custom-classes-transition" enter-active-class="animated slideInLeft">
              <router-view style="animation-duration: 60ms"></router-view>
            </transition>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "测试",
      levelList: [],
      isCollapse: false,
      islogin: this.$store.state.username,
      show: true,
      openeds: ["1"],
      fold: true // 菜单是否折叠
    };
  },
  created() {
    console.log(this.$store.state.username);
  },
  watch: {
    $route(to, from) {
      this.getBreadcrumb();
    }
  },
  mounted() {
    // console.log("mounted");
    this.getBreadcrumb();
  },
  methods: {
    // 菜单栏是否展开
    handleOpen(key, keyPath) {
      console.log(key, keyPath, "handleOpen");
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath, "handleClose");
    },
    gotopath(name) {
      this.$router.push({ name: name });
    },
    //面包屑
    getBreadcrumb() {
      // console.log(this.$route,"$route");
      let matched = this.$route.matched.filter(item => item.meta.title);
      this.levelList = matched;
      // console.log(matched,this.levelList,"levelList")
    },
    exit() {
      // localStorage.removeItem("token");
      sessionStorage.removeItem("token");
      this.$router.push({ name: "login" });
    }
  }
};
</script>

<style lang="scss" scoped>
.el-header {
  padding: 0;
}
.home {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
.el-container {
  height: 100%;
}
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}
.usermes:hover {
  color: white !important;
}
.el-aside {
  width: 240px !important;
  display: block;
  background: rgb(84, 92, 100);
  position: absolute;
  left: 0;
  top: 60px;
  bottom: 0;
}
.isCollapse {
  width: 70px !important;
  display: block;
  background: rgb(84, 92, 100);
}
.copyright {
  border-top: 1px solid white;
  padding: 10px 6px 10px;
  p {
    font-size: 12px;
    height: 23px;
    line-height: 23px;
    color: white;
  }
}
.el-main {
  display: block;
  position: absolute;
  left: 220px;
  right: 0;
  top: 60px;
  bottom: 0;
  background: #f1f1f3;
  padding: 0 !important;
}
.maincollapse {
  display: block;
  position: absolute;
  left: 70px !important;
  right: 0;
  top: 60px;
  bottom: 0;
  background: #f1f1f3;
  padding: 0 !important;
}
.mainView {
  margin: 0 20px 0;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 220px;
  min-height: 400px;
}
.el-menu {
  border: 0;
}
.common {
  margin-right: 5px;
  // margin-left: 15px;
}
.breadcrumb {
  height: 38px;
  display: flex;
  justify-content: flex-start;
  color: #fff;
  background-color: white;
  margin: 10px 20px 15px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
// 头部
.headTop {
  // background:rgba(74, 195, 182, 1);
  background: #409eff;
  height: 60px;
}
.renbenIcon {
  width: 45px;
  margin: 5px 10px 5px;
}
</style>


