// Vue.use(VueAwesomeSwiper)
// Vue.use(VueAxios, Axios);

Vue.use(Vuex);
Vue.use(ElementUI);


Vue.config.productionTip = false

Axios.defaults.baseURL = 'http://10.20.19.26:8097/equity'; //配置你的接口请求地址
// Axios.defaults.headers.common['zbxx-token'] = sessionStorage.getItem("token");//配置token,看情况使用
Axios.defaults.headers.post['Content-Type'] = 'application/json'; //配置请求头信息。

// http request 请求拦截器，有token值则配置上token值

// console.log("token",token);
Axios.interceptors.request.use(
  config => {
    // let token = localStorage.getItem("token");
    let token = sessionStorage.getItem("token");
    if (token) { // 每次发送请求之前判断是否存在token，如果存在，则统一在http请求的header都加上token，不用每次请求都手动添加了
      config.headers.common['zbxx-token'] = token;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
  });

// http response 服务器响应拦截器，这里拦截401错误，并重新跳入登页重新获取token
Axios.interceptors.response.use(
  response => {
    //   console.log('拦截器请求拦截返回参数',response)
    if (response.data.code == "401") {
      // 这里写清除token的代码
      // this.$message.info("登陆时间过期,请重新登陆")
      // console.log("1111111")
      // alert("登陆超时,请重新登陆")
      // localStorage.removeItem("token");
      sessionStorage.removeItem("token");
      router.replace({
        name: 'login',
        query: {
          redirect: router.currentRoute.fullPath
        } //登录成功后跳入浏览的当前页面
      })
    }
    return response;
  },
  error => {
    console.log("拦截器error", error.toString());
    return Promise.reject(error.response.data)
  });
  

