 <template>
  <div class="addEmployee">
    <div class="employeeForm">
      <el-form ref="form" :rules="rules" :model="form" label-width="80px">
        <el-form-item label="公司" prop="company.name">
          <el-input v-model="form.company.name"></el-input>
        </el-form-item>

        <el-form-item label="员工姓名" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="身份证号" prop="idCard">
          <el-input v-model="form.idCard"></el-input>
        </el-form-item>
        <!-- <el-form-item label="工号" prop="jobNumber">
          <el-input v-model="form.jobNumber"></el-input>
        </el-form-item>-->
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择性别">
            <el-option label="男" value="0"></el-option>
            <el-option label="女" value="1"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="岗位" prop="postJob">
          <el-input v-model="form.postJob"></el-input>
        </el-form-item>

        <el-form-item label="入职时间" prop="joinDate">
          <el-date-picker
            v-model="form.joinDate"
            type="date"
            value-format="yyyyMMdd"
            placeholder="选择日"
          ></el-date-picker>
        </el-form-item>
        
        <el-form-item label="人员范围" prop="postArea">
          <el-select v-model="form.postArea" placeholder="请选择职责区域" prop="postArea">
            <el-option label="企业负责人" value="企业负责人"></el-option>
            <el-option label="总经理助理" value="总经理助理"></el-option>
            <el-option label="集团主管" value="集团主管"></el-option>
            <el-option label="专业技术人才" value="主业技术人才"></el-option>
            <el-option label="员工" value="员工"></el-option>
            <el-option label="总经理" value="总经理"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="工龄" prop="seniority">
          <el-input v-model="form.seniority"></el-input>
        </el-form-item>

        <el-form-item label="备注">
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
          <el-button type="primary" @click="exitinput">退出</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "city",
  data() {
    return {
      form: {
        company: {
          name: "" //公司名称
        },
        idCard: "", // 身份证号
        joinDate: "", // 入职时间
        name: "", //员工姓名
        postArea: "", // 人员范围
        postJob: "", // 岗位
        seniority: "", //工龄 单位月
        sex: "" //性别
      },
      rules: {
        "company.name": [
          { required: true, message: "请输入公司名", trigger: "blur" }
        ],
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        postJob: [{ required: true, message: "请输入岗位", trigger: "blur" }],
        joinDate: [
          {
            type: "string",
            required: true,
            message: "请选择日期",
            trigger: "change"
          }
        ],
         date1: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ],
        seniority: [{ required: true, message: "请输入工龄", trigger: "blur" }],
        idCard: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { min: 18, max: 18, message: "请输入18位身份证号", trigger: "blur" }
        ],
        postArea: [
          { required: true, message: "请输入人员范围", trigger: "blur" }
        ]
      }
    };
  },
  created() {},
  methods: {
    //   提交添加员工信息
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log("submit!");
          console.log(JSON.stringify(this.form));
          this.$axios
            .post("/employee/addNewEmployee", JSON.stringify(this.form))
            .then(res => {
              console.log("res", res, "res");
              this.$message.info(res.data.msg);
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          console.log("error submit!!");
          this.$message.info("提交失败,请重新提交");
          return false;
        }
      });
    },

    // 退出
    exitinput() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.addEmployee {
  background: white;
  border-radius: 5px;
  .employeeForm {
    width: 80%;
    margin: 0 auto;
    padding-top: 20px;
  }
}
</style>


