<template>
  <div class="renli">
    <div>
      <!-- <el-button-group>
        <el-button type="primary" @click="pass()">
          审核通过
          <i class="iconfont icon-duihao"></i>
        </el-button>
        <el-button type="primary" @click="nopass()">
          审核不通过
          <i class="iconfont icon-cuohao03"></i>
        </el-button>
      </el-button-group>-->
    </div>
    <div class="renLitable">
      <el-table
        ref="multipleTable"
        :data="tableData"
        stripe
        border
        tooltip-effect="dark"
        style="width: 100%"
        :cell-style="cellHeight"
      >
        <el-table-column prop="employeeName" label="姓名" width="120"></el-table-column>
        <el-table-column prop="employeeSex" width="70" label="性别" ></el-table-column>
        
        <!-- <el-table-column prop="name" label="公司" width="120"></el-table-column> -->
        <el-table-column prop="employeeIdCard" align="center" width="170" label="身份证号" ></el-table-column>
        
        <el-table-column prop="employeeSeniority" width="70" align="center"  label="工龄" ></el-table-column>

        <el-table-column prop="equityShouldMoney" align="center" label="备注">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" @change="handleinput(scope)" placeholder="审核备注"></el-input>
          </template>
        </el-table-column>

        <el-table-column align="center" width="80" label="审核">
          <template slot-scope="scope">
            <el-button  type="text" @click="auditPass(scope.row)">
              <i class="iconfont icon-duihao"></i>
            </el-button>
            <el-button  type="text" @click="auditNoPass(scope.row)">
              <i class="iconfont icon-cuohao03"></i>
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-pagination
      background
      layout="prev, pager, next, sizes, total, jumper"
      :page-sizes="[10, 15, 20, 25]"
      :page-size="10"
      :total="this.total"
      @current-change="handleCurrentChange"
      @size-change="handleSizeChange"
    ></el-pagination>
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
export default {
  data() {
    return {
      msg: "人力资源公司审核测试",
      table: [],
      remark: "",
      tableData: [], // 把人员拿出来放在tableDate数组中
      ids: [], // 选中的ids数组
      auditId: "",
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 从哪一个开始
      pageNum: 10 // 一次请求多少
    };
  },
  created() {},
  mounted() {
    console.log("企权干股资格审核");
    // console.log(sessionStorage.getItem("token"));
    this.check();
    // console.log(this.tableData);
  },
  methods: {
    cellHeight() {
      return "padding:5px 0 !important";
    },
    //   选中获取ids
    handleSelectionChange(val) {
      this.ids = [];
      let len = val.length;
      for (let i = 0; i < len; i++) {
        this.ids.push(val[i].id);
      }
      console.log("ids", this.ids);
    },

    // 请求可审核的单据
    check() {
      this.tableData = [];
      this.$axios
        .get("/audit/list?limit=" + this.pageNum + "&page=" + this.pageSize)
        .then(res => {
          console.log("renli", res);
          this.total = res.data.total;
          this.table = res.data.data;
          for (var j = 0, len = this.table.length; j < len; j++) {
            // console.log(this.table[j].employee);
            // const employee = this.table[j].employee;
            // employee.number = j;
            // employee.id = this.table[j].id;
            if (this.table[j].sex == "0") {
              this.table[j].sex = "男";
            } else {
              this.table[j].sex = "女";
            }
            this.tableData.push(this.table[j]);
            console.log(this.tableData,"tableData");
          }
        })
        .catch(erro => {
          console.log(erro);
        });
    },

    // 分页器
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.check();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.check();
    },

    // 获取审核备注信息
    handleinput(scope) {
      console.log(scope, "scope");
      this.auditId = scope.row.id;
      this.remark = scope.row.remark;
      console.log(this.remark, "actualSendNums");

      // 确认修改实际核算,并请求
      // this.$confirm('此操作将修改实际发放金额, 是否继续?', '提示', {
      //   confirmButtonText: '确定',
      //   cancelButtonText: '取消',
      //   type: 'warning'
      // }).then(() => {
      //   this.actualSend();

      // }).catch(() => {
      //   this.$message({
      //     type: 'info',
      //     message: '已取消修改'
      //   });
      // });
    },

    //   审核通过
    auditPass(row) {
      console.log( row.auditId, "id.....");
      this.$axios
        .put("/audit/" + row.auditId + "/Y", {
          remark: this.remark
        })
        .then(res => {
          console.log("审核通过", res);
          this.check();
        })
        .catch(erro => {
          console.log(erro);
        });
    },
    //   审核不通过
    auditNoPass(row) {
      this.$axios
        .put("/audit/" + row.auditId + "/N", {
          remark: this.remark
        })
        .then(res => {
          console.log("审核不通过", res);
          this.check();
        })
        .catch(erro => {
          console.log(erro);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.renli {
  .renLitable {
    margin-top: 5px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
}
.el-table__header tr,
.el-table__header th {
  padding: 0;
  height: 40px;
}
.el-table__body tr,
.el-table__body td {
  padding: 0;
  height: 40px;
}
</style>


