import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/page/login'
import Home from '@/page/system/home'
import Basic from '@/page/system/home/basic'
import Renli from '@/page/system/home/renli'
import Qiquan from '@/page/system/home/qiquan/qiquan'
import Gangu from '@/page/system/home/gangu/gangu'
import Upload from '@/page/system/home/qiquan/upload'
import Qiquaninput from '@/page/system/home/qiquan/qiquaninput'
import Ganguinput from '@/page/system/home/gangu/ganguinput'
import Qiquansummary from '@/page/system/home/qiquansummary'
import Gangusummary from '@/page/system/home/gangusummary'
import PersonMarage from '@/page/system/home/PersonMarage/PersonMarage'
import AddEmployee from '@/page/system/home/PersonMarage/AddEmployee'

import Account from '@/page/system/account/account'
import Qiquanaccount from '@/page/system/account/qiquanaccount'
import Ganguaccount from '@/page/system/account/ganguaccount'
import QiquanAccountEntering from '@/page/system/account/qiquanAccountEntering'
import GanguAccountEntering from '@/page/system/account/GanguAccountEntering'

import Annualbasinfo from '@/page/system/home/Annualbasinfo'



import AnnualData from '@/page/system/form/annualData'
import Dimission from '@/page/system/form/dimission'
import Jurisdiction from '@/page/system/form/jurisdiction'
import Remind from '@/page/system/form/remind'
import StaffMovement from '@/page/system/form/staffMovement'
import Erro from '@/page/404.vue'

Vue.use(Router)


export default new Router({
    // mode: 'history',
    routes: [{
            path: '/',
            redirect: 'login',
        },
        {
          path: '/login',
          name: 'login',
          component: Login
      },
        {
            path: '/home',
            name: 'home',
            redirect:'personMarage',
            component: Home,
            children:[
              {path: '/basic',
                name: "basic",
                meta:{
                  title:"基本信息管理",
                  requireAuth: true,
              },
                component: Basic,
                redirect: '/personMarage',
                children: [
                  
                {
                  path: '/personMarage',
                  name: "personMarage",                 
                  meta:{
                    title:"人员管理",
                    requireAuth: true,
                  },
                  component: PersonMarage, 
                },
                {
                  path: '/addEmployee',
                  name: "addEmployee",                 
                  meta:{
                    title:"添加员工",
                    requireAuth: true,
                  },
                  component: AddEmployee, 
                },
                  {
                  path: '/renli',
                  name: "renli",
                  meta:{
                    title:"企权干股资格审核",
                    requireAuth: true,
                  },
                  component: Renli,
                },
                {
                  path: '/qiquan',
                  name: "qiquan",
                  meta:{
                    title:"企权基本信息输入",
                    requireAuth: true,
                  },
                  component: Qiquan, 
                  children:[{                
                    path: '/qiquaniput',
                    name: "qiquaninput",
                    component: Qiquaninput, 
                    meta:{
                      title:"增加企权信息",
                      requireAuth: true,
                    },                 
                  }]               
                },


                {
                  path: '/upload',
                  name: "upload",
                  component: Upload, 
                },
                
                {
                  path: '/gangu',
                  name: "gangu",
                  meta:{
                    title:"干股基本信息输入",
                    requireAuth: true,
                  },
                  component: Gangu, 
                  children:[
                    {                  
                    path: '/ganguinput',
                    name: "ganguinput",
                    meta:{
                      title:"增加干股信息",
                      requireAuth: true,
                    },
                    component: Ganguinput,                  
                  }
                ]
                },             
                {
                  path: '/qiquansummary',
                  name: "qiquansummary",
                  meta:{title:"企权汇总表",
                  requireAuth: true,
                },
                  component: Qiquansummary, 
                },
                {
                  path: '/gangusummary',
                  name: "gangusummary",
                  meta:{title:"干股汇总表",
                  requireAuth: true,
                  },
                  component: Gangusummary, 
                },
                {
                  path: '/annualbasinfo',
                  name: "annualbasinfo",
                  meta:{
                    title:"年度基本信息录入",
                    requireAuth: true,
                  },
                  component: Annualbasinfo, 
                },
              ]
              },
              {
                path: '/account',
                name: 'account',
                meta:{title:"核算管理",
                requireAuth: true,
              },
                redirect:'/qiquanaccount',
                component: Account,
                children:[
                {
                  path: '/qiquanaccount',
                  name: 'qiquanaccount',
                  meta:{title:"企权核算",
                  requireAuth: true,
                },
                  component: Qiquanaccount,
                },
                {
                  path: '/qiquanaccenter',
                  name: 'qiquanAccountEntering',
                  meta:{title:"企权实际发放金额录入",
                  requireAuth: true,
                },
                  component: QiquanAccountEntering,
                },

                {
                  path: '/ganguaccount',
                  name: 'ganguaccount',
                  meta:{title:"干股核算",
                  requireAuth: true,
                },
                  component: Ganguaccount,
                },
                {
                  path: '/ganguaccenter',
                  name: 'ganguAccountEntering',
                  meta:{title:"干股实际发放金额录入",
                  requireAuth: true,
                },
                  component: GanguAccountEntering,
                },
               ]
              },
              {
                path: '/annualData',
                name: 'annualData',
                meta:{title:"年度基本信息录入",
                requireAuth: true,
              },
                component: AnnualData,
              },
              {
                path: '/dimission',
                name: 'dimission',
                meta:{title:"离职汇总表",
                requireAuth: true,
              },
                component: Dimission,
              },
              {
                path: '/jurisdiction',
                name: 'jurisdiction',
                meta:{
                  title:"角色权限管理",
                  requireAuth: true,
                 },
                component: Jurisdiction,
              },
              {
                path: '/remind',
                name: 'remind',
                meta:{
                  title:"提醒与警示",
                  requireAuth: true,
                },
                component: Remind,
              },
              {
                path: '/staffMovement',
                name: 'staffMovement',
                meta:{
                  title:"人员调动汇总表",
                  requireAuth: true,
                },
                component: StaffMovement,
              },

          ],
        },             
        {
          path: "/404",
          name: "erro",
          component: Erro
          },           
          {
          path: "*", // 此处需特别注意置于最底部
          redirect: "/404"
          }       
    ]
})


