<template>
  <div class="editEmployee">
     
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-row type="flex" class="row-bg">
        <!-- 表格左边 -->
        <el-col :span="11">
         
          <el-form-item label="公司" prop="company.name">
            <el-input v-model="form.company.name"></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="性别" prop="sex">
            <el-select v-model="form.sex" placeholder="请选择性别">
              <el-option label="男" value="0"></el-option>
              <el-option label="女" value="1"></el-option>
            </el-select>
          </el-form-item>
       
          <el-form-item label="岗位" prop="postJob">
            <el-input v-model="form.postJob"></el-input>
          </el-form-item>
          <el-form-item label="入职时间" prop="joinDate">
            <el-date-picker
              v-model="form.joinDate"
              type="date"
              value-format="yyyyMM"
              @change="change"
              placeholder="选择月"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="工龄" prop="seniority">
            <el-input v-model="form.seniority"></el-input>
          </el-form-item>
        </el-col>
        <!-- 表格右边 -->
        <el-col :span="13">
          <el-form-item label-width="150px" label="身份证号" prop="idCard">
            <el-input v-model="form.idCard" readonly></el-input>
          </el-form-item>

          <el-form-item label-width="150px" label="职责范围" prop="postArea">
            <el-select v-model="form.postArea" placeholder="请选择职责区域">
              <el-option label="企业负责人" value="企业负责人"></el-option>
              <el-option label="总经理助理" value="总经理助理"></el-option>
              <el-option label="集团主管" value="集团主管"></el-option>
              <el-option label="专业技术人才" value="主业技术人才"></el-option>
              <el-option label="员工" value="员工"></el-option>
              <el-option label="总经理" value="总经理"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="备注">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="amend">修改</el-button>
        <el-button type="primary" @click="exit">退出</el-button>
      </el-form-item>
    </el-form>

    <el-button type="primary" @click="refresh">刷新</el-button>


  </div>
</template>

<script>
export default {
  inject:['reload'],
  data() {
    return {
      newEmployee: this.$store.state.newEmployee,  
      form: {
        company: {
          name: this.$store.state.newEmployee.companyName //公司名称
        },
        idCard: this.$store.state.newEmployee.idCard, // 身份证号
        joinDate: this.$store.state.newEmployee.joinDate, // 入职时间
        name: this.$store.state.newEmployee.name, //员工姓名
        postArea:this.$store.state.newEmployee.postArea, // 人员范围
        postJob: this.$store.state.newEmployee.postJob, // 岗位
        seniority: this.$store.state.newEmployee.seniority, //工龄 单位月
        sex: this.$store.state.newEmployee.sex, //性别
        id: this.$store.state.newEmployee.id
      },
      rules: {
        "company.name": [
          { required: true, message: "请输入公司名", trigger: "blur" }
        ],
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        postJob: [{ required: true, message: "请输入岗位", trigger: "blur" }],
        joinDate: [
          {
            type: "string",
            required: true,
            message: "请选择日期",
            trigger: "blur"
          }
        ],
        seniority: [{ required: true, message: "请输入工龄", trigger: "blur" }],
        idCard: [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { min: 18, max: 18, message: "请输入18位身份证号", trigger: "blur" }
        ],
        postArea: [
          { required: true, message: "请输入人员范围", trigger: "blur" }
        ]
      },

    };
  },
  created() {
    console.log("子组件")
  },
  methods: {
    amend() {
      this.$refs.form.validate(valid => {
        if (valid) {
          this.$axios
            .put(
              "/employee/edit",
              JSON.stringify(this.form),
              {
                headers: {
                  "Content-Type": "application/json;charset=UTF-8"
                }
              }
            )
            .then(res => {
              console.log(res, "amend");
              //  this.reload();
              this.$emit("refresh")
              if (res.data.code == "400") {
                this.$message.info(res.data.msg);
              } else {
                this.$message.info(res.data.msg);
              }
            })
            .catch(erro => {
              console.log(erro);
            });
        } else {
          this.$message.info("提交失败,请重新提交");
          return false;
        }
      });
    },

    // 测试刷新
    refresh(){
      this.reload();
    },
    

    change() {
      // 日期选择改变你呢函数
      console.log("change");
    },
    exit() {
      this.$store.state.newEmployeeIsShow = false;
      
    }
  }
};
</script>

<style lang="scss" scoped>
</style>


