<template>
  <div class="gangu" style="position:relative">
    <div>
      <div>
        <el-table 
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        :data="list" 
        highlight-current-row
        border 
        stripe 
        :cell-style="cellHeight"
        style="width: 100%">
          <el-table-column align="center" fixed prop="employeeName" label="姓名"></el-table-column>
          <el-table-column align="center" prop="approvalCode" width="120px" label="批复文号"></el-table-column>
          <el-table-column align="center" prop="auditPeriod" width="140px" label="审计期间"></el-table-column>
          <el-table-column align="center" prop="approvalMoney" width="150px" label="批复金额"></el-table-column>
          <el-table-column align="center" prop="employeeSex" label="性别"></el-table-column>
          <el-table-column align="center" prop="employeeCompanyName" label="公司"></el-table-column>
          <el-table-column align="center" prop="employeeIdCard" width="170px" label="身份证号码"></el-table-column>
          <el-table-column align="center" prop="employeePostArea" width="100px" label="人员范围"></el-table-column>
          <el-table-column align="center" prop="cashDate" label="兑现年度"></el-table-column>
          <el-table-column align="center" prop="auditMonthNumber" width="140px" label="审计区间月份数"></el-table-column>
          <el-table-column align="center" prop="employeeJoinDate" width="150px" label="入职时间"></el-table-column>
          <el-table-column align="center" prop="employeeSeniority" width="50px" label="工龄"></el-table-column>

          <el-table-column align="center" prop="shareMonthNumber" width="140px" label="可享受的月份数"></el-table-column>
          <el-table-column align="center" prop="shareRatio" width="170px" label="审批可享受的干股比例"></el-table-column>
          <el-table-column align="center" prop="sharePerformanceMoney" width="150px" label="可享受的干股金额"></el-table-column>

          <el-table-column align="center" prop="sts" fixed="right" width="100px" label="状态"></el-table-column>
          <el-table-column fixed="right" width="140px" label="操作">
            <template slot-scope="scope">
              <el-button type="text" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
              <el-button type="text" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              <!-- <el-button type="text" @click="handlesubmit(scope.$index, scope.row)">提交</el-button> -->
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          layout="prev, pager, next, sizes, total, jumper"
          :page-sizes="[6, 10, 15, 20]"
          :page-size="10"
          :total="this.total"
          @current-change="handleCurrentChange"
          @size-change="handleSizeChange"
        ></el-pagination>
        <el-row>
          <el-col :span="2">
            <div class="grid-content bg-purple">
              <el-row class="handle">
                <el-button type="primary" @click="add()">增加</el-button>
              </el-row>
            </div>
          </el-col>

          <el-col :span="22">
            <div class="grid-content bg-purple-light">
              <div class="el-botton">
                <el-upload
                  class="upload-demo"
                  :multiple="false"
                  :auto-upload="true"
                  list-type="xls || xlsx"
                  :show-file-list="true"
                  action
                  :limit="1"
                  :on-exceed="handleExceed"
                  :http-request="uploadFile"
                >
                  <el-button type="primary">上传</el-button>
                </el-upload>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
      <div v-if="this.$store.state.gisshow" class="amendmes">
        <Gangumes @refresh="getlist"></Gangumes>
      </div>     
      
    </div>

    <transition
      name="custom-classes-transition"
      enter-active-class="animated slideInLeft"
    >
      <router-view style="background:white;position:absolute;top:0;left:0;right:0;z-index:2000"></router-view>
    </transition>
  </div> 
</template>

<script>
import Gangumes from "./amend/gangumessage";
export default {
  components: {
    Gangumes
  },
   // 重新加载
   provide(){
    return{
      reload:this.reload
    }},

  data() {
    return {
      list: [],
      currentPage1: 1,
      total: 0,
      page: 1,
      pageSize: 1, // 从哪一个开始
      pageNum: 10, // 一次请求多少
      loading: true,
    };
  },
  created() {
    this.$store.state.gisshow = false;
    this.getlist();
  },
  methods: {
    cellHeight() {
      return "padding:2px 0 !important";
    },

  // provide/inject
     reload(){
      this.$store.state.gisshow = false;
      this.$nextTick(function(){
        this.$store.state.gisshow = true;
      })
    },

    getlist() {
      // this.loading = true;
      this.$axios
        .get("/performanceShares/list", {
          params: {
            limit: this.pageNum,
            page: this.pageSize
          }
        })
        .then(res => {
          this.loading = false;
          console.log("干股列表", res);
          this.total = res.data.total;
          this.list = res.data.data;
          for (var i = 0; i < this.list.length; i++) {
            if (this.list[i].employeeSex == "0") {
              this.list[i].employeeSex = "男";
            } else {
              this.list[i].employeeSex = "女";
            }
            switch (this.list[i].sts) {
              case "S":
                this.list[i].sts = "未发放";
                break;
              case "O":
                this.list[i].sts = "发放结束";
                break;
              case "D":
                this.list[i].sts = "作废";
                break;
            }
            this.list[i].shareRatio = this.list[i].shareRatio * 100 + "%";
          }
          console.log(this.list, "list");
        })
        .catch(err => {
          console.log(err);
          //   this.$alert("请求超时，刷新重试！");
          
        });
    },
    // 增加审核用户
    add() {
      this.$router.push({ name: "ganguinput" });
    },

    //

    handleEdit(index, row) {
      console.log("aaa", row);
      this.$store.state.gemployee = row;
      this.$store.state.gisshow = true;
      console.log(this.$store.state.gisshow);
      // console.log(this.gisshow);
    },
    // 删除用户
    handleDelete(index, row) {
      console.log(index, row);
      this.$axios
        .delete("/performanceShares/delete/" + row.employee.id)
        .then(res => {
          console.log(res, "deleate");
          if (res.data.code == "400") {
            this.$message.info(res.data.msg);
          } else if (res.data.code == "200") {
            this.$message.info(res.data.msg);
            this.getlist();
          }
        });
    },
   
    // 分页器
    handleCurrentChange(cpage) {
      console.log(cpage, "cpage");
      this.pageSize = cpage;
      this.getlist();
    },
    handleSizeChange(psize) {
      console.log(psize, "psize");
      this.pageNum = psize;
      this.getlist();
    },

    // 导入文件
    // 上传文件个数超过定义的数量
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 1 个文件，请删除后继续上传`);
    },
    // 上传文件
    uploadFile(item) {
      console.log("item", item);
      const fileObj = item.file;
      console.log(fileObj);
      // FormData 对象
      let form = new FormData();
      // 文件对象
      form.append("file", fileObj);
      console.log("123", form.get("file"));

      this.$axios
        .post(
          "/performanceShares/excelImp",
          form
          // {
          //   headers: { "Content-Type": "multipart/form-data" }
          // }
        )
        .then(response => {
          //   console.log("MediaAPI.upload");
          console.log("response", response);
          //   console.log(this,"this")
          if (response.data.code == "200") {
            this.$message.info("文件：" + fileObj.name + "上传成功");
            this.getlist();
          } else if (response.data.code == "400") {
            this.$message.info(response.data.msg);
          }
        })
        .catch(erro => {
          console.log(erro);
        });
    }
  },
  mounted() {
    console.log(this.$store.state.gisshow,"mounted")
  }
};
</script>

<style lang="scss" scoped>
.handle {
  margin-top: 30px;
}
.upload {
  margin-left: 15px;
}
.upload-demo {
  margin-top: 30px;
}
.el-upload__tip {
  width: 200px;
  margin-left: 10px;
}
.amendmes {
  position: absolute;
  overflow: auto;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 100;
  padding: 15px;
  background: white;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.ganguinput {
  padding: 10px;
  background: white;
  border-radius: 5px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
</style>


