<template>
  <div class="ganguinput">
    <el-form ref="form" :rules="rules" :model="form" label-width="80px">
      <el-row type="flex" class="row-bg">
        <!-- 表格左边 -->
        <el-col :span="11">
          <el-form-item label="行业" prop="industry">
            <el-select v-model="form.industry" placeholder="请选择行业">
              <el-option label="配件业企权干股管理中心" value="配件业企权干股管理中心"></el-option>
              <el-option label="商贸业企权干股管理中心" value="商贸业企权干股管理中心"></el-option>
              <el-option label="成品业机电贸易业企权干股管理中心" value="成品业机电贸易业企权干股管理中心"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="公司" prop="employee.company.name">
            <el-input v-model="form.employee.company.name"></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="employee.name">
            <el-input v-model="form.employee.name"></el-input>
          </el-form-item>
          <el-form-item label="性别" prop="employee.sex">          
          <el-select v-model="form.employee.sex" placeholder="请选择性别">
            <el-option label="男" value="0"></el-option>
            <el-option label="女" value="1"></el-option>
          </el-select>
          </el-form-item>
          <!-- <el-form-item label="工号" prop="employee.jobNumber">
            <el-input v-model="form.employee.jobNumber"></el-input>
          </el-form-item> -->
           <el-form-item  label="身份证号" prop="employee.idCard">
            <el-input
              v-model="form.employee.idCard"         
            ></el-input>
          </el-form-item>

          <el-form-item label="岗位" prop="employee.postJob">
            <el-input v-model="form.employee.postJob"></el-input>
          </el-form-item>
           <el-form-item  label="入职时间" prop="employee.joinDate">
            <el-date-picker
              v-model="form.employee.joinDate"
              type="date"
              value-format="yyyyMMhh"
              placeholder="选择入职时间"
            ></el-date-picker>
          </el-form-item>  
          <el-form-item label="工龄" prop="employee.seniority">
            <el-input v-model="form.employee.seniority"></el-input>
          </el-form-item>
          <el-form-item  label="兑现年度" prop="cashDate">
            <el-date-picker
              v-model="form.cashDate"
              type="year"
              value-format="yyyy"
              placeholder="选择兑现年度"
            ></el-date-picker>
          </el-form-item>         
        </el-col>
        <!-- 表格右边 -->
        <el-col :span="13">        
          <el-form-item label-width="150px" label="职责范围" prop="employee.postArea">
            <el-select v-model="form.employee.postArea" placeholder="请选择人员区域" prop="postArea">
              <el-option label="企业负责人" value="企业负责人"></el-option>
              <el-option label="总经理助理" value="总经理助理"></el-option>
              <el-option label="集团主管" value="集团主管"></el-option>
              <el-option label="专业技术人才" value="主业技术人才"></el-option>
              <el-option label="员工" value="员工"></el-option>
              <el-option label="总经理" value="总经理"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label-width="150px" label="批复文号" prop="approvalCode">
            <el-input v-model="form.approvalCode"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="批复可分配的总金额" prop="approvalMoney">
            <el-input v-model="form.approvalMoney"></el-input>
          </el-form-item>
          <el-form-item label-width="150px" label="审计期间" prop="auditPeriod">
            <el-date-picker
              v-model="form.auditPeriod"
              type="monthrange"
              align="right"
              value-format="yyyyMM"
              unlink-panels
              range-separator="至"
              start-placeholder="开始月份"
              end-placeholder="结束月份"
              >
            </el-date-picker>
          </el-form-item>
          <el-form-item label-width="150px" label="可享受的干股金额" prop="sharePerformanceMoney">
            <el-input v-model="form.sharePerformanceMoney"></el-input>
          </el-form-item>
           <el-form-item label-width="150px" label="可享受的月份数" prop="shareMonthNumber">
            <el-input v-model="form.shareMonthNumber"></el-input>
          </el-form-item>     
          <el-form-item label-width="150px" label="可享受的干股比例" prop="shareRatio" >
            <el-input v-model="form.shareRatio" placeholder="例如:0.14"></el-input>
          </el-form-item> 
          <el-form-item label-width="150px" label="审计期间月份" prop="auditMonthNumber">
            <el-input v-model="form.auditMonthNumber"></el-input>
          </el-form-item>  
        </el-col>
      </el-row>
      <el-form-item style="background:white" label="备注">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item style="background:white">
        <el-button type="primary" @click="onSubmit('form')">提交</el-button>
         <el-button type="primary" @click="exitinput">退出</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        approvalCode: "", // 批复文号
        industry: "", // 行业
        shareMonthNumber:"", // 可享受的月份数
        sharePerformanceMoney:"", //可享受的干股金额
        shareRatio:"", // 享受干股比率 如0.14       
        approvalMoney: "", // 批复金额
        auditMonthNumber:"", // 审计区间月份数
        cashDate: "", // 兑现年度       
        auditPeriod: "", // 审计期间
         employee: {         
          idCard: "",  // 身份证号
          joinDate: "", // 入职时间
          name: "", // 员工姓名
          postArea: "", // 人员范围
          // jobNumber: "", // 工号         
          postJob: "", // 岗位
          seniority: "", // 工龄
          sex: "", // 性别
          sts: "", // 是否在职
          desc:'',// 备注
          company: {
             name: ""
          },
        },
      },
      rules: {
        industry: [
          { required: true, message: "请选择行业", trigger: "change" },
          
        ],
        "employee.company.name": [
                  { required: true, message: '请输入公司名', trigger: 'blur' },
        ],
        "employee.name": [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max:6, message: '请输入2-6个汉字', trigger: 'blur' }
          ],
        "employee.sex": [{ required: true, message: "请选择性别", trigger: "change" }],
        "employee.jobNumber": [{ required: true, message: "请输入工号", trigger: "blur" }],
        "employee.postJob": [{ required: true, message: "请输入岗位", trigger: "blur" }],
        "employee.joinDate": [
          {
            type: "string",
            required: true,
            message: "请选择日期",
            trigger: "blur"
          }
        ],
        auditPeriod:[{required: true, message: "请选择企权期间", trigger: "change" }],
        "employee.seniority": [
          { required: true, message: "请输入工龄", trigger: "blur" },
          // { type: 'number' ,min: 2, max:6, message: '请输入正确工龄', trigger: 'blur' }
          ],
        cashDate: [
          { required: true, message: "请输入兑现年度", trigger: "blur" }
        ],       
        "employee.idCard": [
          { required: true, message: "请输入身份证号", trigger: "blur" },
          { min: 18, max:18, message: '请输入18位身份证号', trigger: 'blur' }
          ],
        "employee.postArea": [{ required: true, message: "请输入人员范围", trigger: "blur" }],
        approvalCode: [{ required: true, message: "请输入批复文号", trigger: "blur" }],
        approvalMoney: [{ required: true, message: "请输入批复可分配的金额", trigger: "blur" }],
        sharePerformanceMoney: [{ required: true, message: "可受企权的总金额", trigger: "blur" }],
        shareMonthNumber: [{ required: true, message: "可享受的月份", trigger: "blur" }],
        shareRatio: [{ required: true, message: "可享受的干股比例", trigger: "blur" }],
        auditMonthNumber: [{required: true, message: "审计期间月份", trigger: "blur" }],

      },
    };
  },
  created() {

  },
  methods: {
    change() {
      // 日期选择改变你呢函数
      console.log("change")
    },
   exitinput(){
      this.$router.push({name:'gangu'});
   },
    // 提交新录入干股的信息
     onSubmit(form) {
       console.log("提交")
     this.$refs.form.validate((valid) => {
          if (valid) {

               console.log("啦啦啦啦啦啦",this.form)
              this.form.auditPeriod = this.form.auditPeriod.join('-');
              // console.log(JSON.stringify(this.form))
              // console.log(this.employee.id)
              // console.log("submit!");
              // console.log(JSON.stringify(this.form))
            this.$axios
              .post("/performanceShares/add", 
                JSON.stringify(this.form),
              )
              .then(res => {
                console.log("res",res,"res");
                this.$message.info(res.data.msg);
              })
              .catch(erro => {
                console.log(erro);
              });   

          } else {
            console.log('error submit!!');
            this.$message.info("提交失败,请重新提交");
            return false;
          }
        });   

    },   
  }
};
</script>

<style lang="scss" scoped>
</style>



